define(['jquery'],
    function ($) {
        return {
            init: function () {
              $('#local-contactlist-toggle')[0].addEventListener("click", function(){
                  var checkbox = $('#localcontactlist-visible')[0];
                  if (checkbox.checked) {
                      checkbox.checked = false;
                  } else {
                      checkbox.checked = true;
                  }
             });
        }
    };
});
